 section{
    clip-path: polygon(76% 85%, 100% 100%, 100% 0, 0 0, 0 100%);
    background-color: black;
    height: 63vh;
}
.side-intero{
    margin-left: 15rem;
    margin-top: 70px;
}

.side-intero img{
    height: 400px;
    width: 350px;
}

 .hero-i{
    display: grid;
    grid-template-columns: 1fr 1fr;
} 


.intro-head{
    color: #ffff;
    margin-top: 15%;
}
.hero-head{
height: 20px;
overflow: hidden;
}
.title-item{
    color: aqua;
}
.title-wrapper{
    animation: move 10s ease-in-out infinite alternate ;
}
@keyframes move{
 25% {transform: translateY(-25PX);

    }
    50% {transform: translateY(-25PX);

    }
}

.intro-head .hero-name h2 span{
    color: aqua;
}
.intro-head .hero-head-dec{
    padding:24px 1px ;
    font-size:15px;
    font-weight:bolder;
}
.title-wrapper {
    padding-bottom: 12px 12px;
}

 .btn button{
    color: black;
    padding: 2px 15px;
    background-color: aqua;
    outline: none;
    border-radius: 2px;
    font-size: 1rem;
    cursor:pointer;
    border: 2px solid aqua;
    transition: all .3s ease-in-out;
}
 .btn button:hover{
    background: black;
    color: white;
    border: 1px solid aqua;
} 

