.button-items{
    color: white;
    height: 40vh;
    width: 45%;
    border: 1px solid gray;
    border-radius: 12px;
    background-color: black;
    position: absolute;
    z-index: 1;
}
@media(min-width:480px){
    .button-items{
        display: none;
    }
}

.button-items .button-menu{
      list-style: none;
      line-height: 50px;
      margin-left: 10px;
  }
  .but button{
    color: black;
    padding: 2px 15px;
    background-color: aqua;
    outline: none;
    border-radius: 2px;
    font-size: 1rem;
    cursor:pointer;
    border-radius: 5px;
    margin-left: 10px;
    border: 2px solid transparent;
    transition: all .3s ease-in-out;
}
.but button:hover{
    background: black;
    color: white;
    border: 1px solid aqua;
}
.button-items .button-menu a{
    text-decoration: none;
    color: #ffff;
}