.resume-1{
    margin-top: 23px;
  }
  .resume-1 h2{
    text-align: center;
  }
  .resume{
    display: grid;
    grid-template-columns: 0.5fr 2fr;
    margin: 0px 1vw;
    background-color: aliceblue;
    /* color: white; */
  }
  
  @media(max-width:480px){
    .resume{
      display: flex;
      flex-direction: column;  
      
    }
  }
  .ICONS{
      display: flex;
      flex-direction: column;
      position: absolute;
  }
  .ICONS ul{
      margin: 20px 0px;
  }
  @media(max-width:480px){
    .ICONS ul{
      margin: 20px 5px;
  }
  }
  
  .resume h2{
    text-align: center;
    margin-top: 23px;
  }
  .resume .resume-button{
    display: flex;
    flex-direction: column;
    width: 200px;
  }
  @media(max-width:480px){
    .resume .resume-button button{ 
     margin:20px;
     width: 100px;
     margin-left: 7rem;
    }
  } 
  
  .resume-button button{
   
        color: black;
        padding: 2px 15px;
        background-color: aqua;
        outline: none;
        border-radius: 2px;
        font-size: 1rem;
        cursor:pointer;
        border: 2px solid aqua;
        transition: all 1s ease-in-out;
        margin: 20px;
        margin-left: 50px;
       
  }
  
  .resume-button button:hover{
   background: black;
   color: white;
   border: 1px solid aqua;
   width: 200px;
  }
  
  .resume  .eduction{
    margin-top:30px ;
  }
  .resume  .eduction ul{
   margin: 12px;
  }
 
  
  .resume .resume-item{
    margin-left: 23px;
  }
  .resume .resume-item .eduction ul  span{
    margin-left:550px;
    color: black; 
  }
  
  .resume .resume-item  .Interests ul li{
    margin: 23px;
    cursor: pointer;
    color: black;
    text-decoration: none;
  }
  
  @media(max-width:480px){
    .resume .resume-item .eduction ul  span{ 
        margin-left:0;
    }
  } 
  @media(max-width:480px){
    .eduction{ 
        margin-left:23px;
    }
    .skill{
      margin-left: 23px;
    }
    .projects{
        margin-left: 23px;
        list-style-type: none;
        /* line-height: 2px; */
    }
    .Interests{
        margin-left: 23px;
    }
  } 