       






.contact {
    text-align: center;
    
}
.contact-heading p{
    font-size:15px;
    font-weight:250;
    }
   

.contact .section-heading span{
    color: aqua;
}
.contact .card-wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

}  
.contact .card-wrapper{
    padding: 30px 23px;
}
.contact .card-wrapper .card{
    display:grid;
    grid-template-columns: 1fr 1fr;
    text-align: center;
    
}
.contact .card-wrapper .card .mobile img{
    width:35px;
    height: 35px;
}
.contact .card-wrapper .card .gmail img{
    width:35px;
    height: 35px;
}

.contact .card-wrapper .input-wrapper input {
  padding: 7px 7px ;
  margin-left: 40px;
  outline: none;
  margin-top: 23px;
  border-radius: 4px;
}
.contact .card-wrapper .div-texarea textarea{
    padding: 7px 7px ;
    margin-left: 40px;
    outline: none;
    margin-top: 23px;
    border-radius: 4px;
}
.btn1{
    margin-left: 40px;
    margin-top: 23px;
    border-radius: 2px;
    outline: none;
}

.card-wrapper .card .mobile h6{
    font-size: 12px;
    font-weight: 100;
}
.card-wrapper .card .gmail h6{
    font-size: 12px;
    font-weight: 100;
}

footer .contact  .btn1 button{
    color: black;
    padding: 2px 15px;
    background-color: aqua;
    outline: none;
    border-radius: 2px;
    font-size: 1rem;
    cursor:pointer;
    border: 2px solid transparent;
    transition: all .3s ease-in-out;
}
footer .contact  .btn1 button:hover{
    background: black;
    color: white;
    border: 1px solid aqua;
}
.bottom{
    background-color: black;
    height: 23vh;
}
.bottom .social svg{
color: white;
}
.bottom{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 12px;
    padding: 2px 3px ;
    flex-direction: column;
}
.bottom .logo {
    display: flex;
    justify-content: center;
    align-items: center;

}
.bottom .logo svg{
    color: aqua;
    box-shadow: 0 1 0 12px white;
    padding: auto 8px;
    margin: 12px;
}
.bottom .social span{
   padding: 1px 5px;
}
.bottom .logo span{
    color: white;
   
}
.bottom .copyright{
    color: white;
} 