.about{
    margin-top:2rem;
   
}

.heading span{
    color: aqua;
}
.about-section{
    display: grid;
    grid-template-columns: 1fr 2fr ;
    height: 60vh;
    width:70%;
    margin-left:14rem;
    box-shadow: 80px 40px 80px rgb(212, 207, 207), 40px 8px 10px white;
}

.about-section:hover {
border: 1px solid aqua;
} 

.content-about{
    margin-top: 40px;
    line-height: 23px;
}
@media(max-width:480px){
  .content-about{
      line-height: 20px;
  }
}
.content-about .pera{
font-size: 15px;
font-weight:500;
}
.content-about .points-higlights {
    margin-top: 23px;
   
}
.content-about .points-higlights li{
    color: black;
    font-size: 15px;
    font-weight:lighter;
}
.content-about .social{
    margin-top: 23px;
}
.content-about .social .Linked svg{
  color: black;
  }
.content-about .social .YouTube svg{
color: black;
}
.content-about .social .gitHub svg{
  color: black;
  }
.content-about .social .facebook svg{
    color: aqua;
}

@media(max-width:480px){
    .about{
      margin-top: 10px;
    }
  }
@media(max-width:480px){
    .about-section{
       display: flex;
       flex-direction: column;
       justify-content: center;
       align-items: center;
       margin-left:1.5rem;
       height: 80vh;
       width: 90%;
    }
  }
  @media(max-width:480px){
    .heading{
      text-align: center;
    }
  }
  @media(max-width:480px){
    .content-about{
       display: flex;
       justify-content: center;
       align-items: center;
       flex-direction:column ;
    }
  }


.about-section .short-img img{
    height: 385px;
    width: 200px;
    box-shadow: 80px 40px 80px rgba(0,0,0,0.35), 40px 8px 10px rgba(0,0,0,0.15);
}
@media(max-width:480px){
  .about-section .short-img img{
    height: 220px;
    width: 150px;
    /* border: 1px solid transparent;
      border-radius:50%; */
      box-shadow: 80px 40px 80px rgba(0,0,0,0.35), 40px 8px 10px rgba(0,0,0,0.15);
  }
}