
.mobile-nav-btn {
    display : none;
}

header{
    background-color: black;
}
header nav {
    padding: 20px 40px 10px 100px;
    display: grid;
    grid-template-columns: 1fr 3fr;
    align-items: center;
    color: white;
    
}

header nav .right-navbar{
    display: flex;
    justify-content: space-between;
}
header nav .right-navbar .nav-links ul{
    display: flex;
    justify-content: space-between;
}
header nav .right-navbar .nav-links ul li{
    margin-right:70px ;
    list-style-type: none;
    position: relative;
    cursor: pointer;
    color: #ffff;
}
header nav .right-navbar .nav-links a{
    text-decoration: none;
    color: #ffff;
}
header nav .right-navbar .nav-links ul li:after{
    content: '';
    position: absolute; 
    left:0;
    bottom: -10px;
    height: 2px;
    width: 0;
    background: white;
    transition: width .3s ease-in-out;
}
header nav .right-navbar .nav-links ul li:hover:after{
    width: 70%;
}
header nav .right-navbar .nav-links ul li.active:after{
    width: 70%;
} 

header nav .right-navbar .btn button{
    color: black;
    padding: 2px 15px;
    background-color: aqua;
    outline: none;
    border-radius: 2px;
    font-size: 1rem;
    cursor:pointer;
    border: 2px solid transparent;
    transition: all .3s ease-in-out;
}
header nav .right-navbar .btn button:hover{
    background: black;
    color: white;
    border: 1px solid aqua;
}
.header-container .logo {
    display: flex;
    justify-content: center;
    align-items: center;
}
.header-container .logo svg{
    color: aqua;
    box-shadow: 0 1 0 12px white;
    padding: auto 8px;
    margin: 12px;
}

@media(min-width:480px){
    .header-container button img{
        display: none;
        }
  }

@media(max-width:480px){
    .header-container button img{
        width: 25px;
        height: 20px;
        }
  }
  @media(max-width:480px){
    header nav{
       display: none;
    }
    .mobile-nav-btn {
        display: block;
    }
  }
