
.project{
    margin-top: 23px;
}

.project .project-head{
text-align: center;
margin-bottom:1rem;
}
.project .project-head span{
    color: aqua;
}

.project  .project-items{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr ;
}

.project  .project-items-a{
    margin: 12px 12px;
    text-align: center;
    color: black;
    /* background-color: black; */
}
.project  .project-items-a a h5{
    color: blue;
}
.project  .project-items-a a img{
    height: 178px;
    width: 284px;
}
.project  .project-items-a  img{
    height: 178px;
    width: 284px;
}
.project  .project-items-a a {
    color: black;
}

@media(max-width:480px){
    .project  .project-items{
        display: grid;
        grid-template-columns: 1fr 1fr ;
        margin-left:10px;
    }
    .project  .project-items-a img{
        height: 150px;
        width: 150px;
    }
    .project  .project-items-a a img {
        height: 150px;
        width: 150px;
    }
}